@import "../../../styles/variables.scss";
@import "../../../styles/mixins.scss";

.header {
  height: $header-footer-height;
  background-color: $main-color;
  z-index: 1;
  position: fixed;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0 17px;
  @include xs-mobile {
    display: flex;
    justify-content: flex-end;
    padding: 0;
  }
  &__logo {
    flex-basis: 15%;
    display: flex;
    align-items: center;
    margin-left: 55px;
    margin-right: 20px;
    @include xs-mobile {
      display: none;
    }
    .logo {
      background-size: contain;
      background-repeat: no-repeat;
      &__icon {
        width: 54px;
        height: 40px;
        background-image: url("../../../assets/logo/white_logo.png");
        margin-right: 10px;
      }
      &__title {
        width: 98px;
        height: 26px;
        background-image: url("../../../assets/logo/white_logo_title.png");
      }
    }
  }
  &__wrapper {
    flex-basis: 85%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__action {
    display: flex;
    justify-content: space-between;
  }
  &__search {
    display: flex;
    align-items: center;
    position: relative;
    min-width: 270px;
    @include xs-mobile {
      min-width: 200px;
      @include landscape {
        min-width: 300px;
      }
    }
  }
  &__search-input {
    opacity: 0.5;
    width: 100%;
    padding: 10px 35px 10px 10px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 4px;
    border: none;
    outline: none;
    transition: 0.5s;
    &:focus {
      opacity: 1;
    }
    @include xs-mobile {
      opacity: 0;
      transition: none;
      &_opened {
        transition: 0.5s;
        opacity: 1;
      }
    }
  }
  &__button {
    border: none;
    background-color: transparent;
    outline: none;
    cursor: pointer;
  }
}

.header .icon {
  width: 48px;
  height: 48px;
  font-size: 30px;
  color: rgb(255, 255, 255);
  padding: 8px 9px;
  margin-right: 13px;
  &__search {
    padding: 10px;
  }
  @include xs-mobile {
    font-size: 28px;
    width: 38px;
    height: 45px;
  }
}

.nav__link {
  display: inline;
  text-decoration: none;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  font-size: 18px;
  margin: 0 15px;
  &:hover {
    color: rgb(247, 241, 241);
  }
  &:first-child {
    margin-left: 0;
  }
  &:last-child {
    margin-right: 0;
  }
  @include xs-mobile {
    display: none;
  }
  @include mobile {
    display: none;
  }
  @include tablet {
    display: none;
  }
}

.btn-search {
  position: absolute;
  opacity: 0.6;
  right: 0;
  .icon {
    width: 30px;
  }
  @include xs-mobile {
    opacity: 1;
    &_opened {
      opacity: 0.6;
    }
  }
}
